.switcher {
    display: flex;

&__light{
color: var( --switch-light-color);
}

&__dark{
 color: var(--switch-dark-color);
}

  &__label {
    display: inline-block;
    margin: 0 12px;
    position: relative;
    vertical-align: middle;
    cursor: pointer;
  }
  &__switch {
    position: relative;
    display: inline-block;
    box-sizing: border-box;
    width: 40px;
    height: 20px;
    border: 1px solid var(--second-background-color);
    border-radius: 25%/50%;
    vertical-align: top;
    background: var(--focus-text-bg-color);
    transition: 0.2s;

    &:before {
      content: '';
      display: inline-block;
      width: 18px;
      height: 18px;
      border-radius: 50%;
      background: var(--second-background-color);
      box-shadow: 0 3px 5px rgba(0, 0, 0, 0.3);
      transition: 0.15s;
    }
  }

  &__input {
  display: block;
  width: 0;
  height: 0;
  position: absolute;
  z-index: -1;
  opacity: 0;

  &:checked + .switcher__switch {
    border-color: var(--focus-text-bg-color);
    background: var(--second-background-color);
  }
  &:checked + .switcher__switch:before {
    background-color: var(--focus-text-bg-color);
    transform: translateX(20px);
  }
  &:checked + .switcher__light{
    color: var(--focus-text-bg-color);
  }
}

}
